<template>
  <div class="category">
    <!-- 分类 -->
    <van-nav-bar title="全部分类" fixed placeholder/>

    <!-- 搜索框 -->
    <van-search readonly shape="round" background="#ffffff" placeholder="请输入搜索关键词" @click="$router.push('/search')" />

    <!-- 分类列表 -->
    <div class="list-box">
      <div class="left">
        <ul>
          <li v-for="(item, index) in list" :key="item.category_id">
            <a :class="{ active: index === activeIndex }" @click="activeIndex = index" href="javascript:;">{{ item.name
            }}</a>
          </li>
        </ul>
      </div>
      <div class="right">
        <div @click="$router.push(`/searchList?categoryId=${item.category_id}`)"
          v-for="item in list[activeIndex]?.children" :key="item.category_id" class="cate-goods">
          <img :src="item.image?.external_url" alt="">
          <p>{{ item.name }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getCategoryData } from '@/api/category'
export default {
  name: 'CategoryPage',
  created () {
    this.getCategoryList()
  },
  data () {
    return {
      list: [],
      activeIndex: 0
    }
  },
  methods: {
    async getCategoryList () {
      const { data: { list } } = await getCategoryData()
      this.list = list
    }
  }
}
</script>

<style lang="scss" scoped>
// 主题 padding
.category {
  padding-bottom: 50px;
  height: 100vh;

  .list-box {
    height: 100%;
    display: flex;

    .left {
      width: 90px;
      height: 100%;
      background-color: #fcfcfc;
      overflow: auto;

      a {
        display: block;
        height: 45px;
        line-height: 45px;
        text-align: center;
        color: #333;
        font-size: 15px;

        &.active {
          color: pink;
          background-color: #fff;
        }
      }
    }

    .right {
      flex: 1;
      height: 100%;
      background-color: #ffffff;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      align-content: flex-start;
      padding: 10px 0;
      overflow: auto;

      .cate-goods {
        width: 33.3%;
        margin-bottom: 10px;

        img {
          width: 70px;
          height: 70px;
          display: block;
          margin: 5px auto;
        }

        p {
          text-align: center;
          font-size: 12px;
        }
      }
    }
  }
}
</style>
